* {
  box-sizing: border-box;
}
body {
  background: #fff;
  color: #353535;
  max-width: 100%;
  min-height: 100%;
}
#header,
#footer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#header,
#navbar {
  position: absolute;
  top: 0;
  left: 0;
}
#header {
  height: 10vh;
  font-size: calc(7px + 2vmin);
  background: #3c6e71;
  color: #d9d9d9;
}
#footer {
  height: 5vh;
  position: fixed;
  bottom: 0;
  right: 0;
  font-size: calc(1px + 2vmin);
  background: #284b63;
  color: #d9d9d9;
}
#footer:hover {
  color: white;
}
#navbar {
  height: 5vh;
  width: 100%;
  margin-top: 10vh;
  display: flex;
  justify-content: center;
  background: #284b63;
  font-size: calc(3px + 2vmin);
  color: #282e63;
}
.nav-links {
  width: 40vw;
  display: flex;
  justify-self: right;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
.nav-link,
.nav-link:visited {
  color: #d9d9d9;
  text-decoration: none;
}
.nav-link:hover {
  color: #3c6e71;
}
#home-sec {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
#board-img {
  max-width: 500px;
  max-height: 500px;
  width: 40vw;
  height: 40vw;
  margin-top: 10%;
}
#home-img-cntr {
  justify-self: center;
}
#home-text {
  justify-self: center;
  margin-left: 5%;
  margin-top: 5%;
}
#feat-let {
  margin-top: 20%;
}
.store-component {
  margin-top: 15vh;
  margin-bottom: 6vh;
}
.shop-item {
  display: flex;
  align-items: center;
  flex-direction: column;
  row-gap: 10px;
  width: 100%;
  min-width: 250px;
  height: 30vh;
  min-height: 250px;
}
#shop-sec {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.letter-disp {
  font-size: calc(30px + 2vmin);
  margin-top: 5%;
  margin-bottom: 1%;
}
#cart-sec {
  display: grid;
  align-items: center;
  justify-content: center;
}
.cart-item {
  border-bottom: 2px solid #3c6e71;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  align-items: center;
  justify-content: center;
  width: 80vw;
  max-width: 900px;
}
.cart-ltr {
  font-size: calc(40px + 2vmin);
  color: #284b63;
  margin-left: 10%;
}
.cart-pts {
  grid-column: span 3;
}
.cart-qty {
  margin-left: 20%;
}
.inner-nav,
.inner-nav:visited {
  grid-column: span 3;
  color: #284b63;
  text-decoration: none;
  text-align: right;
}
.inner-nav:hover {
  color: #282e63;
}
.increase-btn,
.decrease-btn,
.delete-btn {
  border: none;
  display: inline-block;
  font-size: large;
  text-decoration: none;
  background: rgba(255, 0, 0, 0.75);
  color: white;
  width: 3vw;
  height: 3vw;
  max-width: 35px;
  max-height: 35px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  transition: 0.4s;
  margin-top: 2px;
}
.increase-btn,
.decrease-btn {
  background: #142a53;
}
.decrease-btn {
  font-size: larger;
}
.increase-btn:hover,
.decrease-btn:hover {
  background: #284b63;
}
.delete-btn {
  justify-self: end;
  margin-right: 5%;
  grid-column: span 4;
}
.delete-btn:hover {
  background: red;
}
.btn-container {
  display: grid;
  grid-template-rows: 1fr 1fr;
}
#total-container {
  display: grid;
}
#checkout-btn {
  width: 15%;
  min-width: 80px;
  font-weight: bold;
  padding: 0.25em 0.5em;
  text-decoration: none;
  background: #3c6e71;
  color: #d9d9d9;
  transition: .4s;
}
#checkout-btn:hover {
  background: #284b63;
}
#about-sec {
  display: grid;
  justify-content: center;
}
#link-span {
  justify-self: center;
}
#github-img {
  width: 40px;
  height: 40px;
}
